<template>
    <div class="main">

        <div>
            <router-view></router-view>
        </div>

        <van-tabbar v-model="active" @change="tabBarName">
            <van-tabbar-item :icon="item.icon" v-for="(item,index) in tabBarData" :key="index">{{item.title}}</van-tabbar-item>
        </van-tabbar>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                active: 0,

                //底部导航数据
                tabBarData: [
                    {
                        icon: 'home-o',
                        title: '首页',
                        routeName: 'Menu',
                        url:''
                    },
                     {
                        icon: 'orders-o',
                        title: '订单',
                        routeName: 'Order',
                        url:''
                    },
                     {
                        icon: 'shopping-cart-o',
                        title: '购物',
                        routeName: 'Shopcart',
                        url:''
                    },
                     {
                        icon: 'user-circle-o',
                        title: '个人中心',
                        routeName: 'My',
                        url:''
                    },
                ],
                locationURl: ''
            }
        },
        created(){
            this.locationURl=window.location.hash;
            this.isActive();

        },
        methods: {
            tabBarName(index){
                
                this.$router.push({name: this.tabBarData[index].routeName})
            },
            isActive(){
                
                if(this.locationURl.indexOf('menu')!=-1){
                    this.active=0;
                }else if(this.locationURl.indexOf('order')!=-1){
                     this.active=1;
                }else if(this.locationURl.indexOf('shopcart')!=-1){
                    this.active=2;
                }else if(this.locationURl.indexOf('my')!=-1){
                    this.active=3;
                }
            }
        }
    }
</script>

<style lang="less" scoped>

</style>